.todos{
  margin:0;
  padding:0;
  list-style: none;
}

.todo-item{
  margin:0;
  padding:0;
  list-style: none;
  font-size: 24px;
  display: flex;
  align-items: center;
}

.todo-item input{
  display: block;
  width: 20px;
  height:20px;
  margin:0 20px;
}

.todo-item label{
  flex:1;
  padding:15px;
  line-height: 1.2;
  display: block;
}

.todo-item label.complete{
  text-decoration: line-through;
}
.todo-item button{
  border:0;
  outline: 0;
  display: block;
  width: 40px;
  text-align: center;
  font-size: 30px;
  color:#cc9a9a;
  background: #fff;
}